}

.header {
  padding: 0 1.875rem;
  position: fixed;
  inset: 0;
  z-index: 999;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 4.5rem;
  background-color: var(--dark-blue);
}
@media (min-width: 50em) {
  .header {
    padding: 0 3.125rem;
  }
}
@media (min-width: 71.875em) {
  .header {
    padding: 0 7.5rem;
  }
}
.header__hamburger {
  position: relative;
  height: 1.125rem;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.4s ease-in-out;
  z-index: 9999;
}
.header__hamburger span {
  width: 1.5rem;
  height: 0.125rem;
  border-radius: 0.25rem;
  transition: all 0.4s ease-in-out;
  background-color: var(--white);
}
.header__hamburger span::before,
.header__hamburger span::after {
  content: "";
  position: absolute;
  left: 0;
  width: 1.5rem;
  height: 0.125rem;
  border-radius: 0.25rem;
  transition: all 0.4s ease-in-out;
  background-color: var(--white);
}
.header__hamburger span::before {
  top: 0;
}
.header__hamburger span::after {
  bottom: 0;
}
.header__hamburger.active span {
  background: transparent;
}
.header__hamburger.active span::before {
  top: 50%;
  transform: rotate(45deg);
}
.header__hamburger.active span::after {
  top: 50%;
  transform: rotate(-45deg);
}
@media (min-width: 50em) {
  .header__hamburger {
    display: none;
  }
}
.header__nav {
  display: none;
}
@media (min-width: 50em) {
  .header__nav {
    display: block;
  }
  .header__nav .links {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .header__nav .links li + li {
    margin-left: 1.875rem;
  }
  .header__nav .links a {
    color: var(--white);
    font-family: var(--font-text);
    font-weight: 400;
    font-size: 1rem;
  }
}
.header .side-menu {
  display: none;
  transition: all 0.5s ease-in;
}
.header .side-menu .side-links {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
  gap: 0.9375rem;
}
.header .side-menu .side-links li a {
  display: block;
  font-family: var(--font-header);
  font-weight: normal;
  font-size: 1.5rem;
  line-height: 1.8;
  color: var(--white);
  display: block;
  cursor: pointer;
}
@media (min-width: 71.875em) {
  .header .side-menu .side-links li a {
    font-size: 1.75rem;
  }
}
.header .side-menu.active {
  display: flex;
  flex-direction: column;
  position: fixed;
  height: 100%;
  width: 100%;
  z-index: 1500;
  left: 0;
  top: 0;
